.sign__input {
  margin: 0;
  background-color: transparent;
  padding: 0 0 5px 0;
  color: #fff;
  border: 0;
  border-bottom: 2px solid rgba(204, 204, 204, 1);
  font-size: 14px;
  line-height: calc(17/14);
  font-weight: 400;
  width: 100%;
  
}

.sign__input:focus {
  outline: none;
  background-color: transparent;
}