.popup__title {
  width: 100%;
  margin: 0;
  color: #000;
  font-size: 24px;
  line-height: calc(29.05/24);
  font-weight: 900;
  margin-bottom: 14px;
}

@media (max-width:600px) {
  .popup__title {
    font-size: 18px;
    line-height: calc(21.78/18);
    margin-bottom: 74px;
  }
}