.elements {
  width: 100%;
  padding-top: 51px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
  row-gap: 19px;
  column-gap: 17px;
  justify-content: center;
}

@media (max-width: 600px) {
  .elements {
    padding: 36px 19px 0 19px;
  }
}