.header__link {
  margin: 0;
  color: white;
  text-decoration: none;
  font-size: 18px;
  line-height: calc(22/18);
  font-weight: 400;
}

@media (max-width: 600px) {
  .header__link {
    margin-right: 27px;
    font-size: 14px;
    line-height: calc(17/14);
    font-weight: 400;
  }
}